    </van-panel>
    <p class="width"></p>
    <van-cell-group>
      <van-cell title="" value="1111"/>
      <van-cell title=""  :value="list.bedate+ '~'+list.afdate" style="font-size:12px"/>
      <van-cell title="" is-link value=""/>
      <van-cell title="" is-link :value="list.consultanter"/>
    </van-cell-group>
    <p class="width"></p>
    <van-cell :title="list.title" is-link label="" style="font-size:12px;color:black;"/>
    <p class="width"></p>

    <van-row type="flex" justify="center">
      <van-col span="4">
        <img :src="list.img">
