<script>
import axios from "axios";
export default {
  data(){
    return {
      active:0,
      list:''
    }
  },
  methods: {
    onClickLeft() {
      this.$router.push("/main");
    }
  },
  mounted(){
            var that=this
        axios({
      url: "http://101.132.164.103:8080/together/activityid.do",
      params: { id:that.$route.query.id }
    }).then(function(data) {
      console.log(data.data.info)
      that.list = data.data.info;
    });
  }
};
</script>
<style scoped>
.tit div{
  margin:10px 0;
}
.van-cell {
  color: #8f8f90;
}
.van-cell span{
  width: 200px
}
.width {
  background: #f2f2f9;
  height: 10px;
  margin: 0 auto;
}
.van-icon-user-circle-o:before {
  font-size: 26px;
}
.head > p:nth-of-type(3) {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
img {
  width: 38px;
  height: 38px;
}
.van-col {
  text-align: center;
}
a {
  line-height: 40px;
}
p > img,
.li-chi > img {
  width: 260px;
  height: 150px;
}
.van-panel__content {
  margin: 4px 0;
}
.van-panel__header{
  padding:0
}
.list {
  background: gainsboro;
}
.color {
  background: gainsboro;
  margin: 40px 52px;
}
.color a {
  display: inline-block;
  width: 18px;
  height: 18px;
  line-height: 18px;
  background: white;
  border-radius: 50%;
  text-align: center;
  margin-right: 10px;
}
.color .van-col {
  text-align: left;
}
.shadow {
  width: 5rem;
  height: 20px;
  line-height: 20px;
  margin: 20px auto;
  box-shadow: 0px 0px 20px black;
}
h4 {
  margin: 0;
}
.li {
  width: 6rem;
  margin: 10px auto;
}
.li .van-col {
  text-align: left;
  border-left: 1px dashed gainsboro;
  padding-left: 20px;
  position: relative;
}
.li .van-col a {
  display: inline-block;
  width: 30px;
  height: 30px;
  border: 1px solid gainsboro;
  border-radius: 50%;
  position: absolute;
  top: -30px;
  left: -15px;
}
.li-chi {
  margin-top: 20px;
}
.head > p:last-child {
  width: 6rem;
  margin: 0 auto;
  text-align: center;
}
.van-tabbar-item {
  background-color: #e8e8ea;
  color: black;
}
.good2 {
  height: 48px;
  overflow: hidden;
}
.good2 span {
  font-size: 12px;
  line-height: 24px;
  color: rgb(144, 142, 142);
}

</style>



