<template>
  <div class="head">
    <van-nav-bar title="" fixed left-arrow @click-left="onClickLeft"/>
    <van-cell-group>
      <van-field/>
      <van-field v-model="username" label="" placeholder=""/>
      <van-field v-model="sfz" label="" placeholder=""/>
      <van-field v-model="age" label="" placeholder=""/>
      <van-field v-model="sex" label="" placeholder=""/>
      <p class="width"></p>
      <van-field v-model="mm" label="" placeholder=""/>
      <van-field v-model="hy" label="" placeholder=""/>
      <van-field v-model="work" label="" placeholder=""/>
      <p class="width">()</p>
    </van-cell-group>
    <van-row>
      <van-col span="20" offset="2">
        <div contenteditable="true">:</div>
      </van-col>
    </van-row>
    <van-tabbar v-model="active">
      <van-tabbar-item to="tjsh"></van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      username: "",
      sfz: "",
      age: "",
      sex: "",
      mm: "",
      hy: "",
      work: ""
    };
  },
  methods: {
    onClickLeft() {
      Toast("");
    }
  }
};
</script>
<style scoped>
.width {
  background: #f2f2f9;
  height: 0.625rem;
  margin: 0 auto;
}
p:nth-of-type(2) {
  height: 0.875rem;
  line-height: 0.875rem;
  font-size: 12px;
  color: gray;
}
.van-col {
  padding-top: 1.25rem;
  color: gray;
}
div:focus {
  outline: none;
}
.van-tabbar-item {
  background-color: #e8e8ea;
  color: black;
}
</style>



