<template>
	<div>
		<van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft">
		</van-nav-bar>
		<van-cell-group>
			<van-field input clearable v-model="phone" maxlength="11" label="" right-icon="question-o" @click-right-icon="$toast(' 132 | 150 | 152 ')"
			 placeholder="" size="large" />
			<van-field input v-model="password" maxlength="16" type="password" clearable right-icon="question-o"
			 @click-right-icon="$toast('  4 - 16 ')" label="" placeholder="" size="large" />
			<van-field v-model="sms" center clearable label="" placeholder="">
				<van-button class="ziti" slot="button" @click="yzm()" size="small" type="primary">{{yanzh}}</van-button>
			</van-field>
		</van-cell-group>
		<div class="btn">
			<van-button round type="primary" size="large" @click="onClickRegister"></van-button>
		</div>
		<van-row type="flex" justify="center">

			<van-col>&lt;&lt;</van-col>
		</van-row>

	</div>
</template>

<script>
	import axios from "axios";
	import qs from 'Qs'
	import {
		Dialog
	} from 'vant';
	export default {
		name: 'Register',
		data() {
			return {
				phone: "",
				password: "",
				sms: "",
				yanzh: ""
			}
		},
		components: {
			[Dialog.Component.name]: Dialog.Component
		},
		methods: {
			yzm() {

			},
			onClickLeft() {
				this.$router.go(-1);
			},
			onClickRegister() {
				if (this.phone === "") {
					// alert("");
					Dialog.alert({
						message: ''
					}).then(() => {
						// on close
					});
				} else {
					if (this.phone !== "") {
						var reg = /^(132|150|152)\d{8}$/;
						if (!reg.test(this.phone)) {
							// alert("");
							Dialog.confirm({
								title: '',
								message: ''
							}).then(() => {
								// on close
							}).catch(() => {
								// on cancel
							});
						} else {
							if (this.password === "") {
								// alert("");
								Dialog.alert({
									message: ''
								}).then(() => {
									// on close
								});
							} else {
								if (this.password !== "") {
									var reg = /^\w{4,16}$/;
									if (!reg.test(this.password)) {
										// alert("");
										Dialog.confirm({
											title: '',
											message: ''
										}).then(() => {
											// on close
										}).catch(() => {
											// on cancel
										});
									} else {
										var _this = this;
										axios({
											url: "http://101.132.164.103:8080/together/addUser.do",
											params: {
												phone: _this.phone,
												password: _this.password
											}
										}).then(function(data) {
											console.log(data.data)
											if (data.data.code == "1") {
												// alert("");
												Dialog.alert({
													title: '',
													message: ''
												}).then(() => {
													_this.$router.push("/login");// on close
												});
											} else {
												// console.log(data)
												// alert("");
												Dialog.alert({
													title: '',
												}).then(() => {
													// on close
												});
											}
										})
									}
								}
							}
						}
					}
				}
			}
		}
	}
</script>
<style scoped>
	.van-cell-group {
		margin-top: 20px
	}

	.btn {
		margin: 30px 40px;
		color: white;

	}

	.van-col {
		font-size: 16px;
		color: rgb(187, 187, 187);
		text-align: center
	}
</style>



